import { Box, List, ListItem, ListItemText, Link as MuiLink, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import { useMiddleware, useTask } from "../../../Application";
import Indicator from "../../../components/Indicator";
import useFetch from "../../../utils/useFetch";
import useNavigateTask from "../../../utils/useNavigateTask";


const TaskCardListItem = ({ card }) => {

    const navigateTask = useNavigateTask();

    const taskBoardDetails = useTask('Tasks.boardDetails');


    return (
        <ListItem>
            <ListItemText
                primary={card.name}
                secondary={(
                    <>
                        <MuiLink onClick={() => navigateTask(taskBoardDetails(card.board))}><strong>{card.board.name}</strong></MuiLink> {">"} <span>{card.list.name}</span>
                    </>
                )}
            />
        </ListItem>
    )
}


const TasksOverviewWidget = () => {

    const middleware = useMiddleware('Tasks.Cards');

    const {
        data,
        loading,
        reload
    } = useFetch({
        deps: [],
        handler: () => middleware.list(),
        initialState: { data: { data: [], total: 0 }, loading: true }
    })

    const { data: cards } = data;


    if (loading) {
        return (
            <Box sx={{ p: 5, textAlign: 'center' }}><Indicator /></Box>
        )
    }

    if (cards.length < 1) {
        return (
            <Box sx={{ p: 5, textAlign: 'center' }}>موردی جهت نمایش وجود ندارد.</Box>
        )
    }

    return (
        <Box>
            <Typography sx={{ px: 2, my: 2 }} fontWeight={700}>همه‌ی وظایف</Typography>
            <List disablePadding>
                {cards.map((card, index) => (
                    <TaskCardListItem key={index} card={card} />
                ))}
            </List>
        </Box>
    )
}

export default TasksOverviewWidget;